          <h3>Closure</h3>
          <p>
          - makes the eyes complete a path. As long as the eyes has enough information
          the eyes will complete the image without actually seeing every essential line in the picture. This effect works the best with recognizable and simple objects. More advanced objects make it more difficult for the mind to complete the missing lines.
          Closure is often found in paintings and different sculptures. Artists through the ages has managed to study the minds ability to fill in blank spots where vital information are missing.
          </p>
          <h3>Symmetry</h3>
          <p>
